.header {
  display: flex;
  flex-direction: row;
  flex: 1 1;
  justify-content: space-between;
  margin-bottom: 5px;
  height: 60px;
  box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.05);
}

.noSelect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; 
}

/* sticky has a width of 85% because the width of the drawer is 15%. Keep in mind if you change this one you should change the other.
  if you don't change the MenuBoard width, the header will not be centered on scroll
*/
.sticky {
  width: 100%;
  position: fixed;
  z-index: 91;
  top: 0;
}

.headerMenuBar {
  display: flex;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
}

.barIcon {
  padding-left: 20px;
}

.brighIcon{
  padding-right: 20px;
}

.title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.siteTitleLink {
  text-decoration: none;
  cursor: pointer;
}

.headerIcons {
  font-size: 26px;
}

.iconsContainerHeader {
  display: flex;
  justify-content: flex-end;
}

@media only screen and (max-width: 830px) {
  .header {
    height: 30px;
  }

  .barIcon {
    padding-left: 10px;
  }

  .brighIcon{
    padding-right: 10px;
  }

  .title {
    font-size: 10px;
  }

  .headerIcons {
    font-size: 18px;
  }
}
